import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { GermesasPage } from './germesas.page';
import { LoadSpinnerModule } from '../components/utils/load-spinner/load-spinner.module';
import { NovaMesaModule } from '../components/criacoes/nova-mesa.module';
import { InvitationFormModule } from '../components/criacoes/invitation-form.module';
import { ToastModule } from 'primeng/toast';
import { MessageService } from 'primeng/api';
import { FooterModule } from '../components/footer/footer.module';

const routes: Routes = [
  {
    path: '',
    component: GermesasPage
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes),
    LoadSpinnerModule,
    NovaMesaModule,
    InvitationFormModule,
    ToastModule,
    FooterModule
  ],
  providers: [MessageService],
  declarations: [GermesasPage]
})
export class GermesasPageModule { }
