import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MenuController, ModalController } from '@ionic/angular';
import { MesasService } from '../services/mesas/mesas.service';
import { MesaSimplesDTO } from '../models/Mesa/mesaSimples.dto';
import { MesaRegistroService } from '../services/utils/mesa-registro.service';
import { StorageService } from '../services/storage-service.service';
import { NovaMesaComponent } from '../components/criacoes/nova-mesa/nova-mesa.component';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-germesas',
  templateUrl: './germesas.page.html',
  styleUrls: ['./germesas.page.scss'],
})
export class GermesasPage implements OnInit {

  mesas: MesaSimplesDTO[] = []
  imagens: string[] = ['./assets/imgs/brutal-helm.png',
    './assets/imgs/cyborg-face.png',
    './assets/imgs/steampunk-goggles.png',
    './assets/imgs/raise-zombie.png']

  loading: boolean = true;
  showForm: boolean = false;

  constructor(private router: Router, private menu: MenuController,
    private mesaService: MesasService, private mesaRegistro: MesaRegistroService,
    private storageService: StorageService,
    private modalController: ModalController,
    private toastService: MessageService,) { }

  async ngOnInit() {
    this.consultar();

  }

  setImg(opcao: string) {
    switch (opcao) {
      case 'MEDIEVAL_RPG':
        return this.imagens[0]
        break;

      case 'CYBERPUNK_RPG':
        return this.imagens[1]
        break;

      case 'STEAMPUNK_RPG':
        return this.imagens[2]
        break;

      case 'HORROR_RPG':
        return this.imagens[3]
        break;

    }

  }

  acessar(mesa) {
    this.mesaService.setIdMesaSelecionada(mesa.idMesa);
    const idJogador = this.storageService.getLocalUserId();
    if (mesa.idMestre == idJogador) {
      this.storageService.setSelectedMesa(mesa.idMesa, idJogador, 'mestre');
      this.router.navigateByUrl('contmesa');
    } else {
      this.storageService.setSelectedMesa(mesa.idMesa, idJogador, 'jogador');
      this.router.navigateByUrl('ficha');
    }
  }

  async novaMesa() {
    const modal = await this.modalController.create({
      component: NovaMesaComponent,
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',

    });

    modal.onWillDismiss()
      .then(data => {
        if (data.data) {
          this.consultar();
        }
      });
    return await modal.present();
  }

  async consultar() {
    this.loading = true;
    this.mesaService.findListMesasBoth().
      subscribe(res => {
        this.mesas = <any>res;
        this.loading = false;
      }, error => { this.loading = false; });
  }

  juntar() {
    this.showForm = true;
  }

  getNotification(evt) {
    this.showForm = false;
    if (evt != "closed") {
      this.callToast('success', 'Solicitação encaminhada!', 'Sua solicitação foi encaminhada ao Mestre da mesa');
    }
  }

  callToast(severity, summary, detail) {
    this.toastService.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

}
